import React, { useState, useEffect } from "react";
import {
    Table, Badge, Row, Col, Modal,
    ListGroup, ButtonGroup, Button, Form
} from "react-bootstrap";

// Covid App Work:
// 1.add next and prev week data --Done
// 2.add progress/spinner --Done
// 3.remove bug from block list --Done

// 4.check scrollable for div
// 5.css - alignment of thead date --Done
// 6.css = fixed the thead and result to top of page when scroll
// 7.make spinner to overall -- inside model. --Done
// 8.check net connection - if not show from localstroage & msg "net not available" --Done

function CenterTable(props) {

    const [filterlist, setfilterlist] = useState({ "fee": "All", "availbility": "All", "age": "All", "vaccine": "All", "block_name": "All" });
    const [filterCenterList, setfilterCenterList] = useState();
    const [blockSelect, setblockSelect] = useState();
    const [show, setShow] = useState(false);
    const [centDetail, setcentDetail] = useState({ "center": "" });


    useEffect(() => {
        if (localStorage.getItem("filter") !== null &&
            localStorage.getItem("filter") !== "" &&
            localStorage.getItem("filter") !== undefined) {
            setfilterlist(JSON.parse(localStorage.getItem("filter")));
        }
    }, []);

    useEffect(() => {
        setFilter();
        // setShow(false);
        if (Object.keys(filterOptSet).find((sess) => filterlist[sess] !== "All")) {
            // props.setrchstate(false);
        }
        localStorage.setItem("filter", JSON.stringify(filterlist));
    }, [filterlist]);

    useEffect(() => {
        console.log(filterCenterList);
    }, [filterCenterList]);

    useEffect(() => {
        console.log(centDetail);
    }, [centDetail]);

    useEffect(() => {
        setFilter();
    }, [props]);


    const dateset = [];

    const filterOptSet = {
        "fee": [],
        "availbility": ["Available", "Book"],
        "age": [],
        "vaccine": [],
        "block_name": []
    }

    props.centerList.centers.map((cent) => {
        if (!filterOptSet.block_name.includes(cent.block_name)) { filterOptSet.block_name.push(cent.block_name) }
        if (!filterOptSet.fee.includes(cent.fee_type)) { filterOptSet.fee.push(cent.fee_type) }
        cent.sessions.map((sess) => {
            if (!dateset.includes(sess.date)) { dateset.push(sess.date) }
            if (!filterOptSet.vaccine.includes(sess.vaccine)) { filterOptSet.vaccine.push(sess.vaccine) }
            if (!filterOptSet.age.includes(sess.min_age_limit)) { filterOptSet.age.push(sess.min_age_limit) }
        }
        )
    }
    );

    function setFilter() {
        console.log("render");
        let temp = props.centerList.centers;
        // props.setrchstate(false);

        if (filterlist.fee !== "All") {
            temp = temp.filter((center) =>
                center.fee_type.toLowerCase().includes((filterlist.fee).toLowerCase())
            )
        }

        if (filterlist.block_name !== "All") {
            temp = temp.filter((center) =>
                center.block_name == filterlist.block_name
            )
        }

        if (filterlist.age !== "All") {
            console.log(filterlist.age);
            let ageTemp;
            temp = temp.filter((center) => {
                ageTemp = center.sessions.filter(sess => sess.min_age_limit === filterlist.age)
                if (ageTemp.length === 0) {
                    return false;
                }
                center.sessions = ageTemp;
                return true;
            }
            );
            console.log(ageTemp);
        }

        if (filterlist.vaccine !== "All") {
            let ageTemp;
            temp = temp.filter((center) => {
                ageTemp = center.sessions.filter(sess =>
                    sess.vaccine.toLowerCase().includes((filterlist.vaccine).toLowerCase())
                )
                if (ageTemp.length === 0) {
                    return false;
                }
                center.sessions = ageTemp;
                return true;
            }
            );
        }

        if (filterlist.availbility !== "All") {
            let ageTemp;
            temp = temp.filter((center) => {
                ageTemp = center.sessions.filter(sess =>
                    filterlist.availbility === "Available" ?
                        sess.available_capacity > 0 : sess.available_capacity == 0
                )
                if (ageTemp.length === 0) {
                    return false;
                }
                center.sessions = ageTemp;
                return true;
            }
            );
        }

        if (filterlist.fee == "All" && filterlist.age == "All" &&
            filterlist.vaccine == "All" && filterlist.availbility == "All"
            && filterlist.block_name == "All") {
            setfilterCenterList();
        } else {
            setfilterCenterList({ centers: temp });
            localStorage.setItem("filter", JSON.stringify(filterlist));
        }

        console.log(temp);
    }

    function dateformat(date) {
        const dateObject = new Date((date).split("-").reverse().join("/"));
        // const humanDateFormat = dateObject.toLocaleString();
        var option = { day: "numeric", month: "short", year: "numeric" };
        return dateObject.toLocaleString(undefined, option);
    }

    function setFitopt(opt, val) {
        if (opt.split(" ")[0] == "block_name") {
            setblockSelect(val);
            console.log(opt + " = " + val);
        }
        setfilterlist({ ...filterlist, [opt]: val });
    }

    function FilterOpt() {

        return (
            Object.keys(filterOptSet).map(function (opt) {
                if (opt !== "block_name") {
                    return (
                        <div className="mr-2 my-2" key={opt}>
                            <ButtonGroup >
                                <Button size="sm" variant="outline-secondary"
                                    className={filterlist[opt] == "All" ? "active" : "inactive"}
                                    onClick={() => setFitopt(opt, "All")}>All</Button>
                                {
                                    filterOptSet[opt].map((fee) =>
                                        <Button size="sm" variant="outline-secondary" key={opt + "_" + fee}
                                            className={filterlist[opt] == fee ? "active" : "inactive"}
                                            onClick={() => setFitopt(opt, fee)}>{fee}
                                        </Button>
                                    )
                                }

                            </ButtonGroup>
                            <small className="text-muted form-text">{opt}</small>
                        </div >
                    )
                } else {
                    return (
                        <Form.Group controlId="exampleForm.ControlSelect1" className="mr-2 my-2" key={opt}>
                            <Form.Control as="select" value={blockSelect} onChange={(event) => setFitopt("block_name", event.target.value)}>
                                <option>All</option>
                                {
                                    filterOptSet[opt].map((fee) =>
                                        fee ? <option>{fee}</option> : <option>No Block</option>
                                    )
                                }
                            </Form.Control>
                            <small className="text-muted form-text">Block</small>
                        </Form.Group>
                    )
                }

            })
        )

    }

    function restsetFitopt(opt) {
        console.log(opt);
    }

    function CenterDetail() {
        console.log(centDetail);
        return (
            centDetail.center ?
                <Modal show={show} onHide={() => setShow(false)} centered>
                    <Modal.Header closeButton className="py-3">
                        <h5 className="my-auto">Center Details</h5>
                    </Modal.Header>
                    <Modal.Body>
                        <div>
                            <Row>
                                <Col md={6} sm={12} className="mb-4">
                                    <span>
                                        <span className="centerTitle">{centDetail.center.name} </span><br />
                                        <span className="centerSubTitle">{centDetail.center.block_name + ", " + centDetail.center.district_name + ", " + centDetail.center.state_name + "," + centDetail.center.pincode}</span>
                                    </span><br />
                                    <a href={"https://www.google.co.in/maps/search/" + centDetail.center.name} target="blank" className="btn btn-outline-secondary searchBtn">Get Direction</a>
                                </Col>
                            </Row>
                        </div>
                        <hr />
                        {
                            centDetail.center.sessions.map((sess) =>
                                <div className="border border-top-0 border-left-0 border-right-0 py-2">
                                    <div className="d-flex justify-content-between">
                                        <span><h5>Slot Details</h5>
                                            <h5>{dateformat(sess.date)}</h5></span>
                                        <span className="modelList">
                                            <span>{sess.vaccine}</span><br />
                                            {
                                                sess.available_capacity !== 0 ?
                                                    <Badge pill variant="info" className="border">{"Available : " + sess.available_capacity}</Badge>
                                                    : <Badge pill variant="danger" className="border">Booked</Badge>
                                            }
                                            <br /><span>{"Age " + sess.min_age_limit + "+"}</span>
                                        </span>
                                    </div>

                                    <Row>
                                        <Col md={6} sm={12} className="mb-4">
                                            <h6>Time</h6>
                                            <ListGroup className="modeltimeSlotList">
                                                {
                                                    sess.slots.map((time) =>
                                                        <ListGroup.Item>{time}</ListGroup.Item>
                                                    )
                                                }
                                            </ListGroup>
                                        </Col>
                                        <Col md={6} sm={12}>
                                            <h6>Vaccin </h6>
                                            <ListGroup className="list-group-numbered">
                                                <ListGroup.Item className="d-flex justify-content-between align-items-center">
                                                    <span>1st Dose :</span><span>{sess.available_capacity_dose1}</span></ListGroup.Item>
                                                <ListGroup.Item className="d-flex justify-content-between align-items-center">
                                                    <span>2nd Dose :</span><span>{sess.available_capacity_dose2}</span></ListGroup.Item>
                                            </ListGroup>
                                        </Col>
                                    </Row>
                                </div>
                            )
                        }
                    </Modal.Body>
                </Modal>
                : null
        )
    }

    function scrollDiv(dir) {
        if (dir == "L") {
            document.getElementById("wrapper").scrollLeft -= 100;
            console.log(dateset);

            var day = new Date((dateset[0]).split("-").reverse().join("-"));
            console.log(day);

            var nextDay = new Date(day);
            nextDay.setDate(day.getDate() - 6);
            console.log(nextDay.toISOString().split("T")[0].split("-").reverse().join("-"));
            props.searchCal(null, nextDay.toISOString().split("T")[0].split("-").reverse().join("-"));

        } else {
            document.getElementById("wrapper").scrollLeft += 100;
            console.log(dateset);

            var day = new Date((dateset[dateset.length - 1]).split("-").reverse().join("-"));
            console.log(day);

            var nextDay = new Date(day);
            nextDay.setDate(day.getDate() + 1);
            console.log(nextDay.toISOString().split("T")[0].split("-").reverse().join("-"));
            props.searchCal(null, nextDay.toISOString().split("T")[0].split("-").reverse().join("-"));

        }

    }

    const slotDetail = (cent_id, date) => {
        // console.log(cent_id + "=" + sess_id);
        let cent_temp = props.centerList.centers.find((cent) => cent_id === cent.center_id);
        // let sess_temp = cent_temp.sessions.find((sess) => sess_id === sess.session_id);
        // setcentDetail({"center":cent_temp, "session":sess_temp})
        setcentDetail({ ...centDetail, "center": cent_temp, "select_date": date })

        // console.log(cent_temp);
        // console.log(sess_temp);
        setShow(true);
    }

    function TableData(props) {
        return (
            props.data.centers.map((cent) =>
                <tr>
                    <td className="sticky-col first-col">
                        <span className="centerTitle">{cent.name} </span>
                        {cent.fee_type == "Paid" ? <Badge variant="info">{cent.fee_type}</Badge> : <span></span>} <br />
                        <span className="centerSubTitle">{cent.block_name + ", " + cent.district_name + ", " + cent.state_name + "," + cent.pincode}</span>
                        {cent.vaccine_fees ? cent.vaccine_fees.map((fee) => { return (<span className="feetext"><br /><span>{fee.vaccine + " : â‚¹" + fee.fee} </span></span>) }) : null}
                    </td>
                    {
                        dateset.map((datecol) => {
                            let temp;
                            let testVac = [];
                            let testAge = [];
                            let testAvlcount = 0
                            temp = cent.sessions.filter((sess) => datecol === sess.date);
                            temp.map((test) => {

                                if (!testVac.includes(test.vaccine)) { testVac.push(test.vaccine) }
                                if (!testAge.includes(test.min_age_limit)) { testAge.push(test.min_age_limit) }
                                if (test.available_capacity !== 0) { testAvlcount = testAvlcount + test.available_capacity }

                                // console.log(cent.name + " = " + testVac.join(" | ") + " > " + testAge.join(" | ") + " avl " + testAvlcount);
                            })
                            return (
                                <td className="text-center ">
                                    {
                                        temp.length !== 0 ?
                                            <span>
                                                {
                                                    testAvlcount !== 0 ?
                                                        <Badge pill variant="info" className="border">{"Available : " + testAvlcount}</Badge>
                                                        : <Badge pill variant="danger" className="border">Booked</Badge>
                                                }
                                                <br /><span className="centerTitle " > {testVac.join(" | ")}</span><br />
                                                <span className="centerSubTitle ">{"Age " + testAge.join(" | ") + "+"}</span><br />
                                                <Button variant="outline-secondary" size="sm" className="detailBtn"
                                                    onClick={() => slotDetail(cent.center_id,)}>Detail</Button>

                                            </span>
                                            :
                                            <span>
                                                <Badge pill variant="light" className="border">N/A</Badge>
                                            </span>
                                    }

                                </td>
                            )
                        }
                        )
                    }
                </tr>
            )
        )
    }

    return (
        <div>
            <CenterDetail />
            <div className="filterSec">
                <h5>Filter</h5>
                <div className="filterOptSec">
                    <FilterOpt />
                    <div className="mr-2 my-2">
                        <Button size="sm" variant="outline-secondary" className="resetbtn"
                            onClick={() => setfilterlist({ "fee": "All", "availbility": "All", "age": "All", "vaccine": "All", "block_name": "All" })}>Reset
                        </Button>
                    </div>
                </div>
            </div>
            <hr /><br />
            <div >
                <Row><Col className="d-flex my-1 align-items-center" sm={8}>
                    {
                        Object.keys(filterOptSet).find((sess) => filterlist[sess] !== "All")
                            && filterCenterList ?
                            <div className="filterOptSeclist"><h5 className="filterTitleTxt">{filterCenterList.centers.length} - Result For :</h5>
                                {
                                    Object.keys(filterOptSet).map((opt) => {
                                        if (filterlist[opt] !== "All") {
                                            return (
                                                <Button size="sm" variant="outline-dark" key={opt + "_Tag"}
                                                    className="filterTag mx-1" onClick={() => restsetFitopt(opt)}>
                                                    {opt == "age" ? "Age " + filterlist[opt] + "+" : filterlist[opt]}
                                                </Button>
                                            )
                                        }
                                    }

                                    )
                                }</div> : <h5 className="filterTitleTxt">{props.centerList.centers.length + " - Result"}</h5>
                    }
                </Col><Col className="d-flex flex-row-reverse my-1 align-items-center" sm={4}>
                        <ButtonGroup className="ml-2" aria-label="First group">
                            <Button variant="outline-secondary" size="sm"
                                onClick={() => scrollDiv("L")}
                            >Prev</Button>
                            <Button variant="outline-secondary" size="sm"
                                onClick={() => scrollDiv("R")}
                            >Next</Button>
                        </ButtonGroup>
                    </Col>
                </Row>
            </div>
            <div className="wrapper" id="wrapper">
                <Table striped bordered hover>
                    <thead >
                        <tr>
                            <th className="sticky-col first-col">Center</th>
                            {
                                dateset.map((keyName) => {
                                    return (
                                        <th key={keyName} >
                                            {dateformat(keyName)}
                                        </th>
                                    )
                                })
                            }
                        </tr>
                    </thead>

                    {
                        filterCenterList ?
                            <tbody>{console.log("filterlist")}
                                <TableData data={filterCenterList} />
                            </tbody>
                            :
                            props.centerList ?
                                <tbody>{console.log("mianlist")}<TableData data={props.centerList} /> </tbody>
                                : null
                    }

                </Table>
            </div>
            {filterCenterList ?
                filterCenterList.centers.length == 0 ?
                    <h5 className="text-center">No Result Found</h5> : null
                : null
            }
        </div>
    );
}

export default CenterTable;