import React, { useState, useEffect } from "react";
import { Card, Button, Col, Form, Spinner, Alert, Modal } from "react-bootstrap";
import axios from 'axios';
import CenterTable from "./CenterTable";
import 'boxicons';
import Select from 'react-select';

function SearchFrm() {

    const [state, setstate] = useState({ id: "", name: "" });
    const [city, setcity] = useState({ id: "", name: "" });
    const [statelist, setstatelist] = useState();
    const [distlist, setdistlist] = useState();
    const [centerListpss, setcenterListpss] = useState();
    const [options, setoptions] = useState([]);
    const [optionDist, setoptionDist] = useState([]);
    const [progess, setprogress] = useState({ display: false, msg: "" });
    const [network, setnetwork] = useState({ status: true, msg: "Your Online !" });
    const [show, setShow] = useState(false);

    useEffect(() => {
        // getList();
        getLocalValue();
        if (!window.navigator.onLine) {
            setnetwork({ status: false, msg: "Your Offline. plese Check Connection !" });
            setShow(true);
        }

    }, []);

    // useEffect(() => {
    // }, [optionDist]);


    // function getList() {
    //     if (window.navigator.onLine) {
    //         axios.get("https://cdn-api.co-vin.in/api/v2/admin/location/states").then((response) => {
    //             //console.log(stateList.states);
    //             let optarry = [];
    //             response.data.states.map((x) =>
    //                 optarry.push({ value: x.state_id, label: x.state_name })
    //             );
    //             setoptions(optarry);
    //             setstatelist(response.data.states);
    //             localStorage.setItem("statelist", JSON.stringify(response.data.states));
    //             setnetwork({ status: true, msg: "Your Online !" });
    //         });
    //     } else {
    //         let optarry = [];
    //         setstatelist(JSON.parse(localStorage.getItem("statelist")));
    //         JSON.parse(localStorage.getItem("statelist")).map((x) =>
    //             optarry.push({ value: x.state_id, label: x.state_name })
    //         );
    //         setoptions(optarry);
    //         setnetwork({ status: false, msg: "Your Offline. plese Check Connection !" });
    //         setShow(true);
    //     }
    // }

    function getLocalValue() {

        if (window.navigator.onLine) {

            //get State from api
            axios.get("https://cdn-api.co-vin.in/api/v2/admin/location/states").then((response) => {
                //console.log(stateList.states);
                let optarry = [];
                response.data.states.map((x) =>
                    optarry.push({ value: x.state_id, label: x.state_name })
                );
                setoptions(optarry);
                setstatelist(response.data.states);
                localStorage.setItem("statelist", JSON.stringify(response.data.states));

            });

            //get pervState value from local
            if (localStorage.getItem("prevstate") !== null &&
                localStorage.getItem("prevstate") !== "" &&
                localStorage.getItem("prevstate") !== undefined) {

                setstate(JSON.parse(localStorage.getItem("prevstate")));

                //get district list from api
                axios.get("https://cdn-api.co-vin.in/api/v2/admin/location/districts/" + JSON.parse(localStorage.getItem("prevstate")).id).then((response) => {
                    //console.log(response.data);
                    let optarry = [];
                    response.data.districts.map((x) =>
                        optarry.push({ value: x.district_id, label: x.district_name })
                    );
                    setoptionDist(optarry);
                    setdistlist(response.data.districts);
                    localStorage.setItem("distlist", JSON.stringify(response.data.districts));
                });
            }

            //get perv district value 
            if (localStorage.getItem("prevdistrict") !== null &&
                localStorage.getItem("prevdistrict") !== "" &&
                localStorage.getItem("prevdistrict") !== undefined) {
                setcity(JSON.parse(localStorage.getItem("prevdistrict")));

                //get new data with prev district value                
                searchCal(JSON.parse(localStorage.getItem("prevdistrict")).id, getCurrDate());

            }

            setnetwork({ status: true, msg: "Your Online !" });

        } else {

            if (localStorage.getItem("statelist") !== null &&
                localStorage.getItem("statelist") !== "" &&
                localStorage.getItem("statelist") !== undefined) {

                let optarry = [];
                setstatelist(JSON.parse(localStorage.getItem("statelist")));
                JSON.parse(localStorage.getItem("statelist")).map((x) =>
                    optarry.push({ value: x.state_id, label: x.state_name })
                );
                setoptions(optarry);
            }

            //get pervState value from local
            if (localStorage.getItem("prevstate") !== null &&
                localStorage.getItem("prevstate") !== "" &&
                localStorage.getItem("prevstate") !== undefined) {

                setstate(JSON.parse(localStorage.getItem("prevstate")));

            }

            if (localStorage.getItem("distlist") !== null &&
                localStorage.getItem("distlist") !== "" &&
                localStorage.getItem("distlist") !== undefined) {

                let optarry = [];
                setdistlist(JSON.parse(localStorage.getItem("distlist")));
                JSON.parse(localStorage.getItem("distlist")).map((x) =>
                    optarry.push({ value: x.district_id, label: x.district_name })
                );
                setoptionDist(optarry);
            }

            //get perv district value 
            if (localStorage.getItem("prevdistrict") !== null &&
                localStorage.getItem("prevdistrict") !== "" &&
                localStorage.getItem("prevdistrict") !== undefined) {

                setcity(JSON.parse(localStorage.getItem("prevdistrict")));

            }

            if (localStorage.getItem("centerlist") !== null &&
                localStorage.getItem("centerlist") !== "" &&
                localStorage.getItem("centerlist") !== undefined) {
                setcenterListpss(JSON.parse(localStorage.getItem("centerlist")));
            }

            setnetwork({ status: false, msg: "Your Offline. plese Check Connection !" });
            setShow(true);
        }

    }

    function setState(event) {
        if (window.navigator.onLine) {
            setstate({ name: event.label, id: event.value });
            localStorage.setItem("prevstate", JSON.stringify({ name: event.label, id: event.value }));
            localStorage.setItem("prevdistrict", null);
            setcity({ name: "", id: "" });
            setprogress({ ...progess, display: true });
            axios.get("https://cdn-api.co-vin.in/api/v2/admin/location/districts/" + event.value).then((response) => {
                //console.log(response.data);
                let optarry = [];
                response.data.districts.map((x) =>
                    optarry.push({ value: x.district_id, label: x.district_name })
                );
                localStorage.setItem("optarry", JSON.stringify(optarry));
                setoptionDist(optarry);
                setprogress({ ...progess, display: false, msg: "Data get sucessfully" });
            });
            setnetwork({ status: true, msg: "Your Online !" });
        } else {
            setnetwork({ status: false, msg: "Your Offline. plese Check Connection !" });
            setShow(true);
        }

    }

    function setDistrict(event) {
        if (window.navigator.onLine) {
            setcity({ name: event.label, id: event.value });
            localStorage.setItem("prevdistrict", JSON.stringify({ name: event.label, id: event.value }));
        } else {
            setnetwork({ status: false, msg: "Your Offline. plese Check Connection !" });
            setShow(true);
        }

    }

    function searchCal(dis_id, currDate = getCurrDate()) {

        let dis_id_var = city.id;
        if (dis_id) {
            dis_id_var = dis_id;
        }
        if (window.navigator.onLine) {
            setprogress({ ...progess, display: true });
            axios.get("https://cdn-api.co-vin.in/api/v2/appointment/sessions/public/calendarByDistrict?district_id=" + dis_id_var + "&date=" + currDate).then((response) => {
                console.log(response);
                if (response.status === 200) {
                    if (response.data.centers.length !== 0) {
                        setcenterListpss(response.data);
                        localStorage.setItem("centerlist", JSON.stringify(response.data));
                        setprogress({ ...progess, display: false, msg: "Data get sucessfully" });
                    } else {
                        setprogress({ ...progess, display: false, msg: "Sry No Data Found" });
                        alert(progess.msg + " " + response.status)
                    }
                } else {
                    setprogress({ ...progess, display: false, msg: "Network Error" });
                    alert(progess.msg + " " + response.status);
                }
            });

            setnetwork({ status: true, msg: "Your Online !" });
        } else {
            setnetwork({ status: false, msg: "Your Offline. plese Check Connection !" });
            setShow(true);
        }

    }

    function getCurrDate() {
        return new Date().toISOString().slice(0, 10).split("-").reverse().join("-");
    }

    return (
        <div>
            <Card className="bg-white shadow">
                <Card.Body>
                    <Card.Title><h5>Search vaccination center near you.</h5></Card.Title>
                    <Form.Row className="seachfrm">
                        <Col md={4} sm={12}>
                            <Form.Group>
                                <Select options={options} isClearable={true} onChange={setState} isSearchable={true}
                                    value={options ?
                                        options[options.findIndex(x => x.value === parseInt(state.id))]
                                        : { value: state.id, label: state.name }
                                    } />
                            </Form.Group>
                        </Col>
                        <Col md={4} sm={12}>
                            <Form.Group>
                                <Select options={optionDist} isClearable={true} onChange={setDistrict}
                                    value={optionDist ?
                                        optionDist[optionDist.findIndex(x => x.value === parseInt(city.id))]
                                        : { value: city.id, label: city.name }
                                    } />
                            </Form.Group>
                        </Col>
                        <Col md={4} sm={12}>
                            <Button className="searchBtn" onClick={() => searchCal(city.id)} variant="outline-dark">Search</Button>
                        </Col>
                    </Form.Row>
                    <hr />

                    {
                        // progess.display ? <div className="d-flex justify-content-center">
                        //     <Spinner animation="border" role="status">
                        //         <span className="sr-only">Loading...</span>
                        //     </Spinner>
                        // </div> :
                            centerListpss ?
                                <CenterTable
                                    searchCal={searchCal}
                                    centerList={centerListpss}
                                />
                                : <p>Select Sate and Dist</p>
                    }
                </Card.Body>
            </Card>

            <Modal
                size="sm" show={progess.display}
                aria-labelledby="contained-modal-title-vcenter"
                centered className="spinnerModel"
            >
                <Modal.Body>
                    <div className="d-flex justify-content-center">
                        <Spinner animation="border" role="status" variant="dark">
                            <span className="sr-only">Loading...</span>
                        </Spinner>
                    </div>
                </Modal.Body>
            </Modal>

            {
                !network.status ?
                    <div className="fixed-bottom">
                        <Alert show={show} variant="danger" className="m-0"
                            onClose={() => setShow(false)} dismissible>
                            <div className="container d-flex align-items-center">
                                <h5>{network.msg}</h5>
                            </div>
                        </Alert>
                    </div>
                    : null
            }

        </div>
    );
}

export default SearchFrm;